.footer {
  padding: 60px 0;
  color: #fff;
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__text {
    text-align: center;
    font-size: 14px;
    line-height: 115%;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 1220px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1600px) {
  .footer {
    &__img {
      max-width: 800px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    padding: 40px 0;
    &__img {
      max-width: 600px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__img {
      bottom: -32px;
      max-width: 400px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__img {
      max-width: 300px;
    }
  }
}