.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  padding: 34px 0;
  color: #fff;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 45px;
  }
  &__logo {
    flex-shrink: 0;
    font-family: 'Visitor Rus';
    font-size: 32px;
    line-height: 115%;
    letter-spacing: 7.04px;
    span {
      color: #1AD7B8;
    }
  }
  &__menu {
    &-list {
      display: flex;
      align-items: center;
      gap: 45px;
    }
    &-item a {
      position: relative;
      font-family: 'Visitor Rus';
      font-size: 20px;
      line-height: 115%;
      &:after {
        content: '//////';
        position: absolute;
        top: -65px;
        left: 0;
        display: inline-block;
        font-family: 'AKONY';
        font-size: 61px;
        line-height: 100%;
        font-weight: 700;
        color: #1AD7B8;
        opacity: 0;
        pointer-events: none;
        transition: all 0.125s;
      }
    }
    &-item a:hover {
      &:after {
        opacity: 1;
      }
    }
    .active a {
      &:after {
        opacity: 1;
      }
    }
    &-close {
      display: none;
    }
    .header__logo {
      display: none;
    }
  }
}
.header2 {
  .header__menu-list {
    color: #000;
  }
}
.burger {
  position: relative;
  width: 31px;
  height: 24px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #fff;
    border-radius: 5px;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 26px 0;
    &__menu {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.95);
      padding: 150px 15px 50px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      &-list {
        flex-direction: column;
      }
      &-item a:hover {
        color: #1AD7B8;
      }
      &-item a:after {
        display: none;
      }
      &-close {
        position: absolute;
        top: 15px;
        right: 15px;
        width: 29px;
        display: block;
        cursor: pointer;
        img {
          width: 100%;
        }
      }
      .header__logo {
        position: absolute;
        top: 15px;
        left: 50%;
        transform: translate(-50%, 0);
        display: block;
      }
    }
  }
  .header2 {
    .header__menu-list {
      color: #fff;
    }
    .burger {
      span {
        background: #000;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 768px) {
  .header2 {
    .header__logo {
      color: #000;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 15px 0;
    &__logo {
      font-size: 20px;
      letter-spacing: 4.4px;
    }
    &__menu {
      &-list {
        gap: 30px;
      }
    }
  }
}