.main {
  position: relative;
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 260px 0 186px;
  color: #fff;
  &__inner {
    position: relative;
    z-index: 3;
  }
  &__title {
    font-family: 'Visitor Rus';
    font-size: 144px;
    line-height: 115%;
    letter-spacing: 31.68px;
  }
  &__text {
    max-width: 645px;
    font-size: 24px;
    line-height: 120%;
    margin-top: 8px;
    span {
      font-weight: 700;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 45px;
  }
  &__btn {
    width: 405px;
    height: 96px;
    background-image: url("../img/btn-blue__bg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: transparent;
    padding: 10px 20px;
    color: #000;
    &:hover {
      background-image: url("../img/btn-transparent__bg.svg");
      color: #1AD7B8;
    }
    span {
      font-family: 'Visitor Rus';
      font-size: 24px;
      line-height: 115%;
    }
  }
  &__btn2 {
    width: 320px;
    height: 75px;
    background-image: url("../img/btn-transparent__bg2.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: transparent;
    padding: 5px 15px;
    color: #1AD7B8;
    &:hover {
      background-image: url("../img/btn-blue__bg2.svg");
      color: #000;
    }
    span {
      font-family: 'Visitor Rus';
      font-size: 22px;
      line-height: 115%;
    }
  }
  &__items {
    display: inline-grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
    margin-top: 125px;
  }
  &__item {
    max-width: 263px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    background: rgba(18, 18, 18, 0.8);
    padding: 26px;
    &-text {
      font-family: 'Visitor Rus';
      font-size: 24px;
      line-height: 115%;
    }
    &-arrow {
      flex-shrink: 0;
      font-size: 56px;
      line-height: 100%;
      color: #1AD7B8;
    }
  }
  .scroll-down {
    position: absolute;
    top: 760px;
    left: 47px;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 16px;
    &__text {
      transform: rotate(-180deg);
      writing-mode: vertical-rl;
      font-family: 'Visitor Rus';
      font-size: 14px;
      line-height: 115%;
    }
    &__arrows {
      display: flex;
      writing-mode: vertical-rl;
      font-family: 'Visitor Rus';
      font-size: 26px;
      line-height: 100%;
      letter-spacing: 5.72px;
    }
  }
  .sprites {
    position: absolute;
    right: 0;
    bottom: 0;
    pointer-events: none;
  }
  .sprite {
    position: absolute;
    z-index: 2;
    right: 800px;
    bottom: 100px;
    width: 100px;
    animation: sprite 2.4s infinite linear;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .sprite2 {
    position: absolute;
    z-index: 2;
    right: 600px;
    bottom: 100px;
    width: 100px;
    animation: sprite2 2.1s infinite linear;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .sprite3 {
    position: absolute;
    z-index: 2;
    right: 500px;
    bottom: 100px;
    width: 100px;
    animation: sprite3 7.8s infinite linear;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .sprite4 {
    position: absolute;
    z-index: 2;
    right: 400px;
    bottom: 100px;
    width: 100px;
    animation: sprite4 5s infinite linear;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .sprite5 {
    position: absolute;
    z-index: 2;
    right: 300px;
    bottom: 100px;
    width: 100px;
    animation: sprite 3s infinite linear;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .sprite6 {
    position: absolute;
    z-index: 2;
    right: 200px;
    bottom: 100px;
    width: 100px;
    animation: sprite2 4s infinite linear;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .sprite7 {
    position: absolute;
    z-index: 2;
    right: 100px;
    bottom: 100px;
    width: 100px;
    animation: sprite2 6s infinite linear;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.scheme {
  position: relative;
  z-index: 2;
  padding-bottom: 90px;
  color: #000;
  margin-top: -40px;
  &__inner {
    position: relative;
  }
  &__wrap {
    position: relative;
    z-index: 2;
    background: #1AD7B8;
    padding: 30px 60px 20px 60px;
  }
  &__title {
    position: relative;
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 52px / 3));
    gap: 26px;
    margin-top: 90px;
  }
  &__item {
    position: relative;
    background: #fff;
    padding: 75px 25px 25px 25px;
    &-number {
      position: absolute;
      top: -115px;
      left: 35px;
      font-family: 'Visitor Rus';
      font-size: 163px;
      line-height: 100%;
      color: #00584A;
    }
    &-text {
      font-size: 22px;
      line-height: 115%;
    }
    &-info {
      position: absolute;
      top: 10px;
      right: 10px;
      &:hover {
        .scheme__info-hide {
          opacity: 1;
          pointer-events: auto;
        }
      }
    }
  }
  &__info-show {
    width: 36px;
    height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #1AD7B8;
    border-radius: 50%;
    cursor: pointer;
  }
  &__info-hide {
    position: absolute;
    right: 0;
    bottom: calc(100%);
    width: 200px;
    background: #000;
    padding: 10px 20px;
    font-size: 14px;
    line-height: 115%;
    color: #fff;
    opacity: 0;
    pointer-events: none;
  }
  &__subtitle {
    max-width: 915px;
    text-align: center;
    font-family: 'Visitor Rus';
    font-size: 36px;
    line-height: 115%;
    margin: 20px auto 0;
  }
  &__line {
    position: absolute;
    top: -74px;
    left: 0;
    width: 108px;
    height: calc(100% + 124px);
    background: #1AD7B8;
    pointer-events: none;
  }
  &__line2 {
    position: absolute;
    top: -25px;
    left: 5%;
    width: 291px;
    height: calc(100% + 25px);
    background: #1AD7B8;
    pointer-events: none;
  }
  &__line3 {
    position: absolute;
    top: -57px;
    left: 22%;
    width: 72px;
    height: calc(100% + 114px);
    background: #1AD7B8;
    pointer-events: none;
  }
  &__line4 {
    position: absolute;
    top: -25px;
    left: 35%;
    width: 31px;
    height: calc(100% + 25px);
    background: #1AD7B8;
    pointer-events: none;
  }
  &__line5 {
    position: absolute;
    top: -60px;
    left: 38%;
    width: 44px;
    height: calc(100% + 88px);
    background: #1AD7B8;
    pointer-events: none;
  }
  &__line6 {
    position: absolute;
    top: -107px;
    right: 35%;
    width: 80px;
    height: calc(100% + 107px);
    background: #1AD7B8;
    pointer-events: none;
  }
  &__line7 {
    position: absolute;
    top: -74px;
    right: 33%;
    width: 36px;
    height: calc(100% + 74px);
    background: #1AD7B8;
    pointer-events: none;
  }
  &__line8 {
    position: absolute;
    top: -32px;
    right: 11%;
    width: 381px;
    height: calc(100% + 32px);
    background: #1AD7B8;
    pointer-events: none;
  }
  &__line9 {
    position: absolute;
    top: -135px;
    right: 0;
    width: 201px;
    height: calc(100% + 165px);
    background: #1AD7B8;
    pointer-events: none;
  }
  &__img {
    position: absolute;
    top: 20px;
    left: 0;
    width: 870px;
    pointer-events: none;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}
.info {
  padding: 90px 0 60px;
  color: #fff;
  &__items {
    max-width: 1250px;
    display: flex;
    justify-content: space-between;
    gap: 32px;
    margin: 0 auto;
  }
  &__item {
    max-width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-icon {
      max-width: 90px;
      height: 90px;
      margin: 0 auto;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-text {
      font-family: 'Visitor Rus';
      font-size: 24px;
      line-height: 115%;
      margin-top: 38px;
    }
  }
}
.currencies {
  padding: 60px 0 40px;
  color: #fff;
  &__title {
    text-align: center;
  }
  &__subtitle {
    text-align: center;
    font-family: 'Visitor Rus';
    font-size: 36px;
    line-height: 115%;
  }
  &__slider {
    position: relative;
    padding-bottom: 60px;
    margin-top: 50px;
  }
  .swiper-pagination {
    bottom: 0;
    &-bullet {
      width: 7px;
      height: 7px;
      background: #fff;
      border-radius: 0;
      margin: 0 12px !important;
      opacity: 1 !important;
    }
    &-bullet-active {
      background: #1AD7B8;
    }
  }
}
.currency-card {
  &__img {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #1AD7B8;
    padding-bottom: 48%;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 80%;
      max-height: 80%;
    }
  }
  &__text {
    font-family: 'Visitor Rus';
    font-size: 32px;
    line-height: 115%;
    margin-top: 8px;
  }
}
.tariffs {
  padding: 40px 0 80px;
  color: #fff;
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    align-items: center;
    gap: 20px;
    margin-top: 36px;
  }
}
.tariff-card {
  border: 4.5px solid #1AD7B8;
  transition: all 0.3s;
  &:hover {
    background: #fff;
    border-color: #fff;
    color: #000;
    .tariff-card__title {
      opacity: 0.5;
    }
    .tariff-card__btn {
      &:after {
        opacity: 0;
      }
      span {
        color: #000;
      }
    }
  }
  &:nth-child(1) {
    padding: 24px;
  }
  &:nth-child(2) {
    padding: 40px 24px;
  }
  &:nth-child(3) {
    padding: 50px 24px;
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 9.6px;
  }
  &__name {
    font-family: 'Visitor Rus';
    font-size: 64px;
    line-height: 115%;
    color: #1AD7B8;
  }
  &__price {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 36px;
    li {
      display: flex;
      align-items: center;
      gap: 22px;
      i {
        width: 34px;
        flex-shrink: 0;
        img {
          max-width: 100%;
        }
      }
      span {
        font-size: 19px;
        line-height: 120%;
      }
    }
  }
  &__percent {
    font-family: 'Visitor Rus';
    font-size: 84px;
    line-height: 115%;
    color: #1AD7B8;
  }
  &__btn {
    max-width: 380px;
    width: 100%;
    height: 89px;
    padding: 5px;
    margin-top: 10px;
    span {
      font-size: 26px;
    }
  }
}
.connection {
  padding: 80px 0 170px;
  color: #fff;
  &__title {
    text-align: center;
  }
  &__form {
    max-width: 1115px;
    margin: 35px auto 0;
    &-grid {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 16px 20px;
    }
    &-group {
      position: relative;
      overflow: hidden;
      input {
        width: 100%;
        height: 72px;
        background: transparent;
        border: 1px solid #fff;
        padding: 0 35px;
        font-size: 18px;
        line-height: 120%;
        color: #fff;
        &::placeholder {
          color: #fff;
        }
      }
      input:focus {
        background: #fff;
        color: #010101;
      }
      input:focus ~ .input-placeholder {
        color: #010101;
      }
      .input-placeholder {
        position: absolute;
        top: 50%;
        left: 35px;
        transform: translate(0, -50%);
        width: 100%;
        font-size: 18px;
        line-height: 120%;
        white-space: nowrap;
        color: #fff;
        pointer-events: none;
      }
    }
    &-btn {
      max-width: 380px;
      width: 100%;
      height: 89px;
      padding: 5px;
      margin: 40px auto 0;
      span {
        font-size: 26px;
      }
    }
  }
  .form-group--active {
    .input-placeholder {
      display: none;
    }
  }
}

