.api-body {
  position: relative;
  background: #fff;
  &:after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: calc((100vw - 1740px) / 2 + 330px);
    height: 100%;
    background: #000;
    pointer-events: none;
  }
}
.wrapper {
  position: relative;
  overflow: hidden;
}
.h2 {
  font-family: 'Visitor Rus';
  font-size: 70px;
  line-height: 115%;
}
.h3 {
  font-family: 'Visitor Rus';
  font-size: 48px;
  line-height: 115%;
}
.btn-blue__outline {
  position: relative;
  background: #1AD7B8;
  clip-path: polygon( 0% 0%,94.427% 0%,99.979% 23.147%,99.979% 99.455%,5.552% 99.455%,0% 76.308%,0% 0% );
  color: #1AD7B8;
  &:hover {
    color: #000;
    &:after {
      opacity: 0;
    }
  }
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 4px);
    height: calc(100% - 4px);
    background: #000;
    clip-path: polygon( 0% 0%,94.427% 0%,99.979% 23.147%,99.979% 99.455%,5.552% 99.455%,0% 76.308%,0% 0% );
    pointer-events: none;
    transition: all 0.3s;
  }
  span {
    position: relative;
    z-index: 2;
    font-family: 'Visitor Rus';
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: #000;
  backdrop-filter: blur(20px);
  padding: 150px 0;
  color: #fff;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog {
    position: relative;
    max-width: calc(100% - 30px);
    width: 100%;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 0 auto;
      max-width: calc(100% - 30px);
    }
  }
  &__logo {
    position: absolute;
    top: 66px;
    left: 50%;
    transform: translate(-50%, 0);
    flex-shrink: 0;
    font-family: 'Visitor Rus';
    font-size: 32px;
    line-height: 115%;
    letter-spacing: 7.04px;
    span {
      color: #1AD7B8;
    }
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 58px;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__content {
    position: relative;
    z-index: 3;
  }
  &__title {
    text-align: center;
  }
  &__icon {
    width: 100px;
    margin: 0 auto 12px;
    img {
      width: 100%;
    }
  }
  .form-group--active {
    .input-placeholder {
      display: none;
    }
  }
}
@keyframes sprite {
  0% {
    opacity: 1;
    transform: translateX(0px) translateY(0px);
  }
  50% {
    opacity: 1;
    transform: translateX(5px) translateY(-156px);
  }
  100% {
    opacity: 0;
    transform: translateX(0px) translateY(-209px);
  }
}
@keyframes sprite2 {
  0% {
    opacity: 1;
    transform: translateX(0px) translateY(0px);
  }
  52% {
    opacity: 1;
    transform: translateX(5px) translateY(-156px);
  }
  100% {
    opacity: 0;
    transform: translateX(-33px) translateY(-219px);
  }
}
@keyframes sprite3 {
  0% {
    opacity: 1;
    transform: translateX(0px) translateY(0px);
  }
  50% {
    opacity: 1;
    transform: translateX(5px) translateY(-180px);
  }
  100% {
    opacity: 0;
    transform: translateX(-33px) translateY(-260px);
  }
}
@keyframes sprite4 {
  0% {
    opacity: 1;
    transform: translateX(0px) translateY(0px);
  }
  50% {
    opacity: 1;
    transform: translateX(5px) translateY(-200px);
  }
  100% {
    opacity: 0;
    transform: translateX(-33px) translateY(-419px);
  }
}

@media screen and (max-width: 1740px) {
  .api-body {
    &:after {
      width: 330px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .api-body {
    &:after {
      width: 300px;
    }
  }
  .h2 {
    font-size: 48px;
  }
  .h3 {
    font-size: 32px;
  }
  .modal {
    &__close {
      width: 40px;
    }
  }
}

