@media screen and (max-width: 768px) {
  .api-body {
    &:after {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .modal {
    padding: 100px 0;
    &__logo {
      top: 28px;
      font-size: 20px;
    }
  }
}

