.text-page {
  padding: 170px 0 110px;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 67px;
  }
  &__sidebar {
    position: relative;
    width: 270px;
    flex-shrink: 0;
    padding-right: 30px;
    color: #fff;
    &-list {
      position: relative;
      z-index: 3;
      display: grid;
      grid-template-columns: 100%;
      gap: 30px;
      li a {
        font-family: 'Visitor Rus';
        font-size: 24px;
        line-height: 115%;
        &:hover {
          color: #1AD7B8;
        }
      }
    }
  }
  &__content {
    width: calc(100% - 337px);
  }
  &__text {
    h3 {
      font-family: 'Visitor Rus';
      font-size: 48px;
      line-height: 115%;
      margin-bottom: 20px;
    }
    p {
      font-size: 20px;
      line-height: 115%;
    }
    b {
      width: 100%;
      height: 40px;
      display: block;
      opacity: 0;
      pointer-events: none;
    }
  }
}

@media screen and (max-width: 1200px) {
  .text-page {
    &__inner {
      gap: 30px;
    }
    &__sidebar {
      &-list {
        li a {
          font-size: 20px;
        }
      }
    }
    &__content {
      width: calc(100% - 300px);
    }
    &__text {
      h3 {
        font-size: 32px;
        margin-bottom: 15px;
      }
      p {
        font-size: 16px;
      }
      b {
        height: 30px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .text-page {
    padding: 120px 0 60px;
    &__sidebar {
      &-list {
        li a {
          font-size: 16px;
        }
      }
    }
    &__text {
      h3 {
        font-size: 24px;
        margin-bottom: 10px;
      }
      p {
        font-size: 14px;
      }
      b {
        height: 20px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .text-page {
    &__inner {
      flex-direction: column;
    }
    &__sidebar {
      width: 100%;
      color: #000;
      &-list {
        grid-template-columns: repeat(2, calc(50% - 30px / 2));
      }
    }
    &__content {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .text-page {
    padding: 80px 0 50px;
  }
}